import React, { useEffect } from 'react';
import { Loader } from 'shared/components';
import { useAppState } from 'shared/state';
import { useNotistack, useRouter } from '../../shared/hooks';

function Home() {
	const { areUnreadsFetched, unreadLoadingError } = useAppState();
	const { history } = useRouter();
	const { showNotification } = useNotistack();

	useEffect(() => {
		if (Object.values(areUnreadsFetched).every((type) => type)) {
			history.push('/initial-loader');
		}
	}, [areUnreadsFetched]);

	useEffect(() => {
		if (unreadLoadingError !== null) {
			showNotification({
				message: `Unread count: ${unreadLoadingError}`,
				variant: 'error'
			});
			history.push('/case-messages');
		}
	}, [unreadLoadingError]);

	return (
		<div className={'flex h-screen w-full items-center justify-center'}>
			<Loader />
		</div>
	);
}

export default Home;
